'use strict';

import $ from 'jquery';
import toastr from 'toastr';

// Constructor
var LoginForm = function () {

  $('#loginForm').validator().on('submit', function (e) {
    if (e.isDefaultPrevented()) {
      toastr.error('Please check the form');
      return;
    } else {
      e.preventDefault();
    }

    var form = {
      'email': $('#loginEmail').val(),
      'password': $('#loginPassword').val()
    };

    $.ajax({
      type: 'POST',
      url: kit.userEndpoint + '/token',
      data: JSON.stringify(form),
      contentType: "application/json",
      dataType: 'json'
    }).done(function (e) {
      localStorage.setItem('token', e.uuid);
      kit.loggedIn = true;

      window.location.href = '/?msgType=success&msg=You\'re now logged in.';
    }).fail(function (e) {
      toastr.error('Couldn\'t log you in - are you sure your e-mail/password are correct?');

      localStorage.removeItem('token');
      kit.loggedIn = false;
      window.update();
    });
    return false;
  });

};

module.exports = LoginForm;
