'use strict';

import $ from 'jquery';

// Constructor
var Navbar = function () {

  $('.logout').click(function (e) {
    localStorage.removeItem('token');
    kit.loggedIn = false;
    kit.currentUser = null;

    window.location.href = '/?msgType=success&msg=You\'re logged out.';
  });

  this.update = function () {
    if (kit.currentUser != null) {
      $('#user-dropdown').html(kit.currentUser.email);
    }
  };

};

module.exports = Navbar;
