'use strict';

import $ from 'jquery';
import toastr from 'toastr';
import Navbar from '../_modules/navbar/navbar';
import LoginForm from '../_modules/login-form/login-form';
import RegistrationForm from '../_modules/registration-form/registration-form';

$(() => {
  /* Hacks for loading bootstrap */
  window.jQuery = $;
  require('bootstrap');
  require('bootstrap-validator');

  /* Initialize components */
  new LoginForm();
  new RegistrationForm();
  // new Subscription();

  /* Enrich AJAX requests with auth token */
  $.ajaxSetup({
    beforeSend: (xhr) => {
      var token = localStorage.getItem('token');
      if (token) {
        xhr.setRequestHeader('Authorization', 'Bearer ' + token);
      }
    }
  });

  window.navbar = new Navbar();

  window.kit = {
    userEndpoint: 'https://api.07Kit.com/user',
    currentUser: null,
    loggedIn: false,

    queryParam: (key) => {
      var qd = {};
      location.search.substr(1).split("&").forEach(function (item) {
        var s = item.split("="),
          k = s[0],
          v = s[1] && decodeURIComponent(s[1]);
        (qd[k] = qd[k] || []).push(v)
      });
      return qd[key];
    }
  };

  window.retrieveUser = () => {
    $.get(kit.userEndpoint).done((e) => {
      console.log(e);
      kit.loggedIn = true;
      kit.currentUser = e;
      window.update();
    }).fail((e) => {
      kit.loggedIn = false;
      localStorage.removeItem('token');
      window.update();
    });
  };

  window.init = () => {
    window.retrieveUser();
    window.update();

    var msgType = kit.queryParam('msgType');
    var msg = kit.queryParam('msg');
    if (msgType && msg) {
      if (msgType[0] === 'info') {
        toastr.info(msg[0]);
      } else if (msgType[0] === 'success') {
        toastr.success(msg[0]);
      } else if (msgType[0] === 'error') {
        toastr.error(msg[0]);
      } else {
        console.log("Unrecognized message type.");
      }
    }
  };

  window.update = () => {
    navbar.update();

    if (kit.loggedIn) {
      $('.not-logged-in').hide();
      $('.logged-in').show();
    } else {
      $('.logged-in').hide();
      $('.not-logged-in').show();
    }
  };

  window.init();

});
